/* =======================================================
  *
  * 	Template Style 
  *
  * ======================================================= */
body {
  font-family: "Work Sans", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: #5f5b5b;
}

#page {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

a {
  color: #3c0b04;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

a:hover,
a:active,
a:focus {
  color: #3c0b04;
  outline: none;
  text-decoration: none;
}

p {
  margin-bottom: 20px;
}

h1,
h2 {
  color: #000;
  font-family: "Work Sans", Arial, sans-serif;
  font-weight: 400;
  margin: 0 0 20px 0;
}

::-webkit-selection {
  color: #fff;
  background: #3c0b04;
}

::-moz-selection {
  color: #fff;
  background: #3c0b04;
}

::selection {
  color: #fff;
  background: #3c0b04;
}

.fh5co-nav {
  /* position: absolute; */
  background-color: rgba(0, 0, 0, 0);
  top: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  padding: 30px 0;
  z-index: 1001;
}

@media screen and (max-width: 768px) {
  .fh5co-nav {
    padding: 20px 0;
  }
}
.fh5co-nav #fh5co-logo {
  font-size: 40px;
  margin: 0;
  /* margin-left: 2rem; */
  padding: 0;
  line-height: 40px;
  font-family: "Mansalva", cursive;
}

.fh5co-nav #fh5co-logo img {
  height: 80px;
}

@media screen and (max-width: 768px) {
  .fh5co-nav #fh5co-logo img {
    height: 50px;
  }
}
.fh5co-nav a {
  padding: 5px 10px;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .fh5co-nav .menu-1 {
    display: none;
  }
}
.fh5co-nav ul {
  padding: 0;
  margin: 2px 0 0 0;
}

.fh5co-nav ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  display: inline;
}

.fh5co-nav ul li a {
  font-size: 22px;
  padding: 30px 10px;
  text-transform: uppercase;
  color: #3c0b04;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.fh5co-nav ul li a:hover,
.fh5co-nav ul li a:focus,
.fh5co-nav ul li a:active {
  color: #3c0b04;
}

.fh5co-nav ul li.active > a {
  color: #ee462c !important;
}

@media screen and (min-width: 769px) {
  .container.longer {
    width: 90%;
  }
}
.fh5co-social-icons {
  margin: 0;
  padding: 0;
}

.fh5co-social-icons li {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -moz-inline-stack;
  display: inline-block;
  zoom: 1;
  *display: inline;
}

.fh5co-social-icons li a {
  display: -moz-inline-stack;
  display: inline-block;
  zoom: 1;
  *display: inline;
  color: #3c0b04;
  padding-left: 10px;
  padding-right: 10px;
}

.fh5co-social-icons li a i {
  font-size: 20px;
}
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


#fh5co-footer,
#fh5co-couple-story {
  padding: 3em 0;
  clear: both;
}

@media screen and (max-width: 768px) {
  
  #fh5co-footer,
  #fh5co-couple-story {
    padding: 3em 0;
  }
}
@media screen and (max-width: 1023px) {
  #fh5co-first {
    padding: 3em 0;
    clear: both;
  }
}
.heart {
  position: absolute;
  top: 4em;
  left: 0;
  right: 0;
  z-index: 99;
  -webkit-animation: pulse 1s ease infinite;
          animation: pulse 1s ease infinite;
}

.heart i {
  font-size: 20px;
  background: #fff;
  padding: 20px;
  color: #3c0b04;
  border-radius: 50%;
}

@media screen and (max-width: 768px) {
  .heart {
    display: none;
  }
}

.fh5co-heading {
  margin-bottom: 1em;
}

.fh5co-heading h2 {
  font-size: 60px;
  margin-bottom: 10px;
  line-height: 1.5;
  font-weight: bold;
  color: #3c0b04;
  font-family: "Amatic SC", cursive;
}

@media screen and (max-width: 768px) {
  .fh5co-heading h2 {
    font-size: 40px;
  }
}
.fh5co-heading p {
  font-size: 18px;
  line-height: 1.5;
  color: #828282;
}

.fh5co-heading span {
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.4);
}
#fh5co-footer .copyright .block {
  display: block;
}

.gototop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.gototop.active {
  opacity: 1;
  visibility: visible;
}

.gototop a {
  width: 50px;
  height: 50px;
  display: table;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  border-radius: 4px;
}

.gototop a i {
  height: 50px;
  display: table-cell;
  vertical-align: middle;
}

.gototop a:hover,
.gototop a:active,
.gototop a:focus {
  text-decoration: none;
  outline: none;
}

@media screen and (max-width: 768px) {
  .fh5co-nav {
    /* display: none; */
  }
}

.js .animate-box {
  opacity: 0;
}

h2 {
  font-size: 40px;
}

h1 {
  font-size: 85px;
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1340px;
  }
}

.event-poster {
  border-radius: 7px;
}

.event {
  font-size: 22px;
}

@media screen and (max-width: 1024px) {
  .event-poster img {
    width: 100%;
  }
}
@media screen and (min-width: 1024px) {
  .event-poster {
    height: 85vh;
  }

  .event-poster img {
    height: 100%;
    /* width: auto; */
  }

  .event #page {
    padding: 12vh 4vh 3vh;
    margin-bottom: 5em;
  }
}
.event-about {
  padding: 30px;
}

.event-about h1 {
  font-family: "Amatic SC", cursive;
  font-size: 80px;
}

.event a {
  font-size: 28px;
}

.event-about ul {
  padding: 0;
  margin-top: 3em;
}

@media screen and (max-width: 1024px) {
  .event-about ul {
    padding: 0;
    margin-top: 1em;
  }
}
.event-about ul li {
  list-style: none;
  display: inline;
  margin-left: 10px;
  margin-right: 10px;
}

@media screen and (max-width: 500px) {
  .event {
    font-size: 18px;
  }
}

.fh5co-heading h1 {
  font-family: "Amatic SC", cursive;
  font-size: 90px;
  color: #3c0b04;
}

@media screen and (max-width: 500px) {
  .fh5co-heading h1 {
    font-size: 70px;
  }
}

.litfest-rules {
  font-size: 20px;
}

#page {
  min-height: 100vh;
}

@media screen and (min-width: 768px) {
  .second-prize {
    border-left: 2px solid black;
  }
}
.prizes h1 {
  color: #3c0b04;
  font-family: "Quicksand", sans-serif;
  font-weight: 100;
}

.prizes h2 {
  /* font-size:30px; */
}

